@import './common/palette';
@import './common/variables';

// 글로벌 scss import
@import './global/_color';


.select {
  // width: max-content;
  // max-width: 260px;
  &.round{
    .select__control{
      @include rounded(4px);
    }
  }
  &__control{
    @include rounded(0);
    border: 1px solid var(--ce7);
    padding: 0 10px;
    min-height: 35px !important;
    &--is-focused{
      border-color: var(--primary-b);
    }
    &:hover{
      border-color: var(--primary-b);
    }
  }
  &__placeholder{
    @include body2(var(--c99));
  }
  &__input{
    // display: none;
  }
}