@charset "UTF-8";
@import './_fonts.scss'; // 폰트 설정

*,
*::before,
*::after {
  box-sizing: border-box;
}
:root {
  overflow-wrap: break-word;
  word-break: keep-all;
  -webkit-font-smoothing: antialiased;

  &:lang(ja),
  &:lang(zh_CN),
  &:lang(zh_TW) {
    word-break: break-all;
  }
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
textarea,
p,
blockquote,
th,
td,
input,
select,
textarea,
button {
  margin: 0;
  padding: 0;
}
dl,
ul,
ol,
menu,
li {
  list-style: none;
}
address,
caption,
cite,
code,
dfn,
em,
var,
i {
  font-style: normal;
}
img,
fieldset,
iframe {
  border: 0 none;
}
img {
  max-width: 100%;
  height: auto;
  -webkit-backface-visibility: hidden;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
input,
select,
textarea,
button {
  vertical-align: middle;
}
textarea {
  resize: none;
}
input::-ms-clear {
  display: none;
}
button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
}
button,
input[type='button'],
input[type='submit'],
input[type='reset'],
input[type='file'] {
  border-radius: 0;
  -webkit-appearance: button;
}
input[type='image'],
input[type='text'],
input[type='submit'],
input[type='password'],
input[type='search'] {
  border-radius: 0;
  -webkit-appearance: none;
}
input:checked[type='checkbox'] {
  -webkit-appearance: checkbox;
  background-color: transparent;
}
input[type='search']::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
}
caption,
legend {
  position: absolute;
  top: -9999px;
  left: -9999px;
  font-size: 0px;
  line-height: 0;
}
a {
  text-decoration: none;
}
a:active {
  background-color: transparent;
}
textarea,
input {
  appearance: auto; // 그리드 체크박스 확인을 위해 auto 로 변경
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
main,
menu {
  display: block;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
sup,
sub {
  line-height: 0;
}

ul:after,
ol:after {
  content: '';
  clear: both;
  display: block;
}

summary::marker {
  font-size: 0;
  display: none;
}

html,
body {
  width: 100%;
  font-size: 10px;
  line-height: 1;
  font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-weight: 400;
  background-color: var(--bg);
}
body {
  font-size: 1.2rem;
  letter-spacing: -0.1px;
}
